import React from 'react'
import { useSelector } from 'react-redux'
import Dropdown from 'Component/Dropdown'
import Decimal from 'Component/Input/decimal'
import Input from 'Component/Input'
import Note from 'Component/Input/Note'
import { useEffect } from 'react'


const WeightEntry = ({
    entryListIdx,
    wmsPoSkuInfoId,
    gradeList,
    ripeness,
    inputWidth,
    showTitle,
    item,
    onChange,
    onChangeDropdown,
    uom,
    poListIdx,
    category,
    areaDestination
}) => {
    let ripenessLevel = useSelector(state => state.ripenessLevel)
    let areaMovement = useSelector(state => state.areaMovement)
    let rip = []
    let filteredGradeList = gradeList
    useEffect(() => {
        const w = ripenessLevel.findIndex(r => r.value === 'waste')
        const sw = ripenessLevel.findIndex(r => r.value === 'semi_waste')
        if (w === -1 && areaDestination === 'Waste') ripenessLevel.push({ label: "Waste", value: "waste" })
        if (sw === -1 && areaDestination === 'Waste') ripenessLevel.push({ label: "Semi Waste", value: "semi_waste" })
    }, [])
    if (category !== 'Fruits') rip = ripenessLevel.filter(r => r?.value === 'waste' || r?.value === 'semi_waste')
    if (category === 'Fruits') rip = ripenessLevel


    if (areaMovement?.value === 8) filteredGradeList = gradeList?.filter(grade => grade.label === 'Waste' || grade.label === 'Semi Waste')
    return (
        <div className='d-flex w-100 we-m'>

            <Decimal
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                title={'Input Weight'}
                showTitle={showTitle}
                onChange={(e) => onChange(e)} />

            <Input
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                title={'Input Quantity'}
                showTitle={showTitle}
                onChange={(e) => onChange(e)} />
            <Dropdown
                // isDisabled={entryListIdx === 0 ? true : false}
                isEmpty={item.gradeIsEmpty}
                selectedValue={item?.grade}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={true}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'grade' })}
                title='Grade'
                placeholder='Select Grade'
                options={filteredGradeList}
                width='w-30'
                className={areaDestination === 'Waste' ? null : 'd-none'}
                showTitle={showTitle} />
            <Dropdown
                // isDisabled={entryListIdx === 0 ? true : false}
                isEmpty={item.ripenessIsEmpty}
                selectedValue={item?.ripeness}
                grade={item.grade?.label}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={true}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'ripeness' })}
                title='Select Ripeness'
                placeholder='Select Ripeness'
                options={rip}
                width='w-30'
                showTitle={showTitle}
                className={areaMovement?.value === 3 ? null : 'd-none'} />
            <Note
                isEmpty={item.noteIsEmpty}
                grade={'Waste'}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                ripeness={ripeness}
                inputWidth={`w-50 ${item.grade?.label === 'Semi Waste' || item.grade?.label === 'Waste' ? null : 'd-none'}`}
                values={item?.note}
                showTitle={showTitle}
                title={'Note'}
                onChange={(e) => onChange(e)} />

        </div>
    )
}
export default WeightEntry